.page-footer {
  background-color: $basic_white;
  border-radius: 30px 30px 0 0;
  margin-top: -30px;
  padding-top: 37px;
  position: relative;
  z-index: 2;

  @media (min-width: $tablet-width) {
    border-radius: 60px 60px 0 0;
    margin-top: -54px;
    padding-top: 0;
    padding-bottom: 24px;
  }
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 40px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 60px;
  }
}

.page-footer__logo {
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    width: 164px;
    height: 42px;
    margin-top: 55px;
    margin-bottom: 16px;
  }

  @media (min-width: $desktop-width) {
    width: 200px;
    height: 55px;
    margin-top: 50px;
  }
}

.page-footer__social-list {
  margin: 0;

  @media (min-width: $tablet-width) {
    padding-top: 10px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 16px;
  }
}

.page-footer__developed {
  display: flex;
  justify-content: space-between;
  margin-top: 36px;
  align-self: stretch;
  border-top: 1px solid $footer_grey_light;

  @media (min-width: $tablet-width) {
    border: none;
    margin-top: 50px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 38px;
  }
}

.page-footer__developed-span {
  font-size: 16px;
  line-height: 16px;
  color: $special_grey;
  padding-top: 20px;
  padding-bottom: 27px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
    padding-top: 15px;
    margin-right: 25px;
    margin-left: 7px;
  }
}

.page-footer__developed-link {
  display: block;
  width: 27px;
  height: 33.3px;
  margin-top: 12px;
  background-image: url("../img/htmlacademy.svg");
  background-repeat: no-repeat;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}
